a{
    color: black;
    cursor: pointer;
}

.socialContact {
    display: flex;
    margin: 1vmin 1vmin 0 1vmin
}

.linkContact{
    display: flex;
    font-size: 5vh;
}

.icons {
    font-size: 5vmin;
}

.contacts {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: antiquewhite;
}

.editContact {
    display: flex;
    align-items: center;
    align-self: start;
    width: 100%;
    min-height: 4vh;
    padding: .3vw;
    background-color: blanchedalmond;
    border-radius: 1.5vh; 
}

.contactName {
    display: flex;
    min-width: 25%;
    background-color: #1e70bf;
    border-radius: 1vw 0 0 1vw;
}

.linkEditContact {
    display: flex;
    max-width: 75%
}

.linkEditInput {
    display: flex;
    min-height: 3vh;
}

:global(#mainLink){
    display: inline-block;
    width: 12vmax;
    height: 5vmax;

#mainLink:hover {
    color: #1e70bf;
    text-decoration: none;
}
}