:global(#profile-form){

    display: flex;
    flex-direction: column;
    min-width: 100%;
    max-height: 100%;


    .fullName {
        display: flex;
        align-items: center;
        width: 100%;
        min-height: 4vh;
        padding: .3vw;
        background-color: blanchedalmond;
        border-radius: 1.5vh;
    }

    .fullName input{
        width: 75%;
        margin-left: 5px;
        padding: .2vw;
    }

    .lookingForJob {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        align-self: flex-start;
        width: 100%;
        min-height: 4vh;
        padding: .3vw;
        background-color: blanchedalmond;
        margin-top: 1vh;
        border-radius: 1.5vh;
    }

    .lookingForJob input {
        margin-left: 5px;
        padding: .2vw;
    }

    .lookingForJobDescription {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        align-self: flex-start;
        width: 100%;
        min-height: 4vh;
        padding: .3vw;
        background-color: blanchedalmond;
        margin-top: 1vh;
        border-radius: 1.5vh;
    }

    .lookingForJobDescription input {
        width: 75%;
        margin-left: 5px;
        padding: .2vw;
    }
    
    .aboutMe {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        align-self: flex-start;
        width: 100%;
        min-height: 4vh;
        padding: .3vw;
        background-color: blanchedalmond;
        margin-top: 1vh;
        border-radius: 1.5vh; 
    }

    .aboutMe input {
        width: 75%;
        margin-left: 1vmin;
        padding: .2vw;
    }

.socialContact {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-self: flex-start;
    width: 100%;
    min-height: 4vh;
    padding: .3vw;
    background-color: blanchedalmond;
    margin-top: 1vh;
    border-radius: 1.5vh; 
}

}