:global(#infoStatus){
    height: 5%;
    
    textarea {
        display: block;
        min-height: 2em;
        width: 70%;
        margin-left: auto;
        margin-right: auto;
        resize: none;
        overflow: hidden;
        border-bottom: 1px solid grey;
        border-right: 1px solid gray;
    }

    .status {
        display: block;
        width: 60%;
        margin: 1em 1em 1em 1em;
        word-wrap: break-word;
    }

    .status:hover {
        background-color: honeydew;
    }

    .editMode {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 100%;
        min-height: 6em;
        border-radius: 4em;
        border-bottom: 2px solid grey;
        border-right: 2px solid gray;
        background-color: white;
    }

    .editStatus {
        margin: 0.5em 1em 0 1em;
    }

    .statusButton {
        display: flex;
        align-self: center;
        justify-content: center;
        align-items: center;
        width: 60%;
        min-height: 2em;
        padding: 0;
        margin-top: 0.5em;
        border-bottom: 2px solid grey;
        border-right: 2px solid gray;
        background-color: whitesmoke;
    }

    .statusButton:hover{
        background-color: rgb(102, 137, 202);
        color: white;
    }
}