:global(#profileData) {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    border-radius: 2vh;
    background-color: whitesmoke;
    padding: 3%;

    .dataComponent{
        display: flex;
        justify-content: left;
        align-items: center;
        padding: 1vmin;
        margin-bottom: 1vmin;;
        border-radius: 1vmin;
        background-color: rgb(213, 219, 224);
    }

    .dataComponentContacts{
        display: flex;
        flex-direction: column;
        height: 30vmin;
    }

    .dataInfo {
        word-break: break-word;
        margin-right: 1vmin;
    }

    .contacts {
        display: flex;
        height: 4vmin;
    }

    .componentContact{
        display: flex;
        flex-wrap: wrap;
        flex: 1;
        justify-content: space-evenly;
        justify-self: center;
        min-width: 25vmin;
    }

    .editButton {
        display: flex;
        width: 100%;
        max-height: 4vh;
        margin-top: auto;
        border-radius: 1vmin;
        background-color: white;
        justify-content: center;
        align-items: center;
        font-size: 1.3rem;
    }

    .editButton:hover {
        cursor: pointer;
        color: white;
        background-color: rgb(71, 114, 194);;
    }
}