.postsBlock {
    padding: 2%;
    width: 85%;
}

.text {
    height: auto;
}

.posts {
    display:flex;
    flex-direction: column;
    width: 100%;
    margin-top: 5%;
    padding-left: 2%;
}

.post {
    margin-bottom: 2%;
    padding: 2%;
    border-radius: 1.6em;
    background-color: white;
}

.line {
    border-bottom: 2px solid rgb(163, 161, 161);
}

:global(#newPostForm){ 

    .form {
        display: flex;
    }

.inputArea {
    display: flex;
    align-items: center;
    min-height: 5em;
    width: 100%;
    background-color: white;
    border-radius: 1.6em;
}

.borderButton {
    display: flex;
    justify-content: center;
    width: 15%;
    border-radius: 45% 20% 20% 45%;
    border-right: 2px solid rgb(71, 114, 194);
}

.borderButton::after {
    content: '';
    display: block;
    position: relative;
    width: 1%;
    height: 2.5em;
    top: 0.8em;
    right: -0.17em;
    border-right: 3px solid white;
}

.borderButton:hover {
    background-color: rgb(102, 137, 202);
}

.textWrapper {
    display: flex;
    width: 30vw;
}

.borderTextarea {
    display: block;
    min-height: 4em;
    width: 3%;
    border-radius: 120em 0 0 120em;
    border-left: 2px solid rgb(71, 114, 194);
}

.postText {
    min-height: 4em;
    width: 100%;
    margin: 0;
    resize: none;
    overflow: hidden;
}

.smilesButtonWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 4em;
    margin: 0;
    padding: 0;
    border-radius: 45% 20% 20% 45%;
    background-color: white;
}

.smilesButtonWrapper:hover{
    background-color: rgb(102, 137, 202);
}

.buttonSmiles {
    font-size: 4vmin;
    border-radius: 45%;
    background-color: yellowgreen;
}

.buttonWrapper {
    display: flex;
    justify-content: flex-end;
}

.addPost {
    margin: 0.5em 0.5em 1em 1em;
    background-color: whitesmoke;
    font-size: medium;
}

.addPost:hover{
    background-color: rgb(71, 114, 194);
    color: whitesmoke;
}


}