:global(#postContainer){
.postContainer {
  display: flex;
  position: relative;
  width: 100%;
  height: 15%;
}

.postAuthorInfo {
  display: flex;
  width: 100%;
  height: 10%;
  margin-right: 0.3em;
}

.avatar {
  display: flex;
  height: 10%;
  width: 10%;
}

.authorData {
  padding: 2em;
}

.postDate {

}

.line {
  margin-top: 2px;
  border-bottom: 2px solid rgb(213, 219, 224);
}

.likesWrapper {
  display: flex;
  justify-content: right;
  width: 100%;
  height: 100%;
  margin-top: 0.5em;
}



  .likeButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.5em;
    height: 1.5em;
    border-radius: 45%;
    margin: 0;
    padding: 0;
    background-color: white;
  }

  .likeButton:hover {
    background-color: rgb(71, 114, 194);
  }

  .likeIcon {
    display: block;
    font-size: 1.3em;
    border-radius: 45%;
    background-color: red;
    color: green;

  }
}

.likeCount {
  font-family: fantasy;
}

.postWrapper {
  width:100%;
}

.postContent {
  width: 100%;
    color: rgb(57, 33, 190);
    word-wrap: break-word;
  }

  .avatar img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }